
.about__container{
    margin-top: 2rem;
    display: grid;
    grid-template-columns: 35% 50%;
    gap: 15%;
    place-items: center;
  
}


.about__me{
    width: 80%;
    aspect-ratio: 1;
    border-radius: 2rem;
    background: linear-gradient(45deg,var(--color-primary),transparent);
    display: grid;
    place-items: center;
}

.about__me-image{
   height: 100%;
    overflow: hidden;
    border-radius: 2rem;
    transform: rotate(10deg);
    transition: var(--transition);
}

.about__me-image:hover{
    transform: rotate(0deg);
}

.about__cards{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.2rem;
}


.about__card{
    background-color: transparent;
    border-radius: 1rem;
    border : 1px solid var(--color-light);
    padding: 2rem;
    text-align: center;
    transition: var(--transition);
}

.about__icon{
    color: var(--color-primary);
    font-size: 1.4rem;
    margin-bottom: 1rem;
}

.about__card h5{
    font-size: 0.95rem;
}

.about__card small{
    font-size: 0.53rem;
    color: var(--color-light);
}

.about__content p{
margin: 2rem 0  2.6rem;
color: var(--color-light);
}

/* =========== MEDIA QUERIES (MEDIUM DEVICES) =========== */
@media screen and (max-width: 1024px) {


    .about__container{
        grid-template-columns: 1fr;
        gap: 0;
    }

    .about__me{
        width: 50%;
        margin: 2rem auto 4rem;
    }

    .about__container p{
        margin: 1rem 0 1.5rem;
    }

    .about__content{
        text-align: center;
    }
    .about__cards{
        gap:1rem;
    }
}

/* =========== MEDIA QUERIES (SMALL DEVICES) =========== */
@media screen and (max-width: 600px) {
    
    .about__me{
        margin: 0 auto 3rem;
    }
    
    .about__cards{
        grid-template-columns: 1fr 1fr;
    }
    
    .about__content p{
        margin:  1.5rem 0;
    }

    .about__container a{
        margin: 1rem 0 2rem;
    }
}